'use strict';

/**
* Create an array containing uniformly distributed pseudorandom numbers between `0` and `1`.
*
* @module @stdlib/random-array-randu
*
* @example
* import randu from '@stdlib/random-array-randu';
*
* var arr = randu( 10 );
* // returns <Float64Array>
*
* @example
* import randu from '@stdlib/random-array-randu';
*
* var arr = randu( 10, {
*     'dtype': 'generic'
* });
* // returns [...]
*
* @example
* import randu from '@stdlib/random-array-randu';
*
* var rand = randu.factory();
* // returns <Function>
*
* var arr = rand( 10 );
* // returns <Float64Array>
*
* @example
* import randu from '@stdlib/random-array-randu';
*
* var rand = randu.factory();
* // returns <Function>
*
* var arr = rand( 10, {
*     'dtype': 'generic'
* });
* // returns [...]
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;

// exports: { "factory": "main.factory" }
